@import "../../../sass/variable.scss";

.inbox__main{
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
    height: 75vh;
    // padding: 1rem;
    .message__container{
        .heading{
            text-align: center;
            padding: 10px;
            h2{
                font-size: $fs-4;
                font-family: $font-domine;
            }
        }
        .message__box{
            // margin-top: 1rem;
            .message__row.active{
                background-color: #000;
            }
            .message__row{
                cursor: pointer;
                display: flex;
                align-items: center;
                gap: 15px;
                background-color: $footer-back;
                padding: 10px;
                border-radius: 5px;
                .imgBox{
                    position: relative;
                    width: 50px;
                    height: 50px;
                    
                    // background-color: #333;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        border-radius: 50%;
                        border: 1px solid #333;
                        background-color: #333;
                    }
                    .active{
                        content: "";
                        width: 8px;
                        height: 8px;
                        background-color: rgb(90, 247, 90);
                        border-radius: 50%;
                        position: absolute;
                        top: 7%;
                        right: 6%;
                    }
                }
                .name__message{
                    p{
                        padding-bottom: 5px;
                        font-size: $fs-7;
                    }
                    span{
                        font-size: $fs-8;
                    }
                }
            }
        }
    }

    .seller__inbox__main{
        height: 100%;
        .seller__inbox__column{
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .seller__row{
                position: relative;
                display: flex;
                align-items: center;
                gap: 15px;
                background-color: #f1f1f1;
                padding: 10px;
                .imgBox{
                    position: relative;
                    width: 45px;
                    height: 45px;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        border-radius: 50%;
                        border: 1px solid #333;
                        background-color: #333;
                    }
                    .active{
                        content: "";
                        width: 8px;
                        height: 8px;
                        background-color: rgb(90, 247, 90);
                        border-radius: 50%;
                        position: absolute;
                        top: 7%;
                        right: 6%;
                    }
                }
                .name__message{
                    p{
                        padding-bottom: 2px;
                        font-size: $fs-7;
                    }
                    span{
                        font-size: $fs-9;
                        color: #333;
                    }
                }
                .back__icon{
                    position: absolute;
                    top: 50%;
                    right: 2%;
                    transform: translate(0, -50%);
                    cursor: pointer;
                    color: #333;
                }
            }
            .messages__box{
                overflow-y: auto;
                flex-basis: 100%;
                padding: 0 10px 0 10px;
                .message__row{
                    width: 100%;
                    margin-top: 5px;
                    width: max-content;
                    .message__content{
                        display: flex;
                        align-items: center;
                        gap: 10px;
                        background-color: #333;
                        padding: 5px 5px;
                        border-radius: 5px;
                        color: #fff;
                        img{
                            width: 100px !important;
                            height: 120px !important;
                        }
                        p{
                            font-size: $fs-8;
                        }
                    }
                    .time__ago{
                        p{
                            font-size: $fs-10;
                            color: $spanish-gray;
                            margin-top: 2px;
                        }
                    }
                }
                .row__2{
                    margin-left: auto;
                   
                }
            }
            .send__messages{
                background-color: #f1f1f1;
                padding: 10px;
                form{
                    display: flex;
                    justify-content: center;
                    gap: 10px;
                    .side__input{
                        position: relative;
                        flex-basis: 8%;
                        overflow: hidden;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        input{
                            visibility: hidden;
                            position: absolute;
                        }
                        label{
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            color: #333;
                            cursor: pointer;
                        }
                    }
                    .center__input{
                        flex-basis: 84%;
                        input{
                            padding: 10px 20px;
                            border: 1px solid #dadada;
                            border-radius: 20px;
                            font-size: $fs-7;

                            &:focus{
                                outline: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px){
    .inbox__main{
        height: 88vh;
    }
}